.ma__location-icons {
  @include clearfix;

  &__item {
    text-align: center;
    margin-bottom: 30px;

    @media ($bp-x-small-max) {
      @include span-columns(2 of 6);
      @include omega(3n);
    }

    @media ($bp-x-small-min) {
      @include span-columns(1 of 4);
      @include omega(4n);
    }
  }

  &__icon {
    height: 40px;
    margin-bottom: 18px;
    text-align: center;

    & > svg {
      height: 100%;
      width: 100%;
    }
  }

  &__name {
    font-size: 0.813rem;
    margin-bottom: 0;
    text-transform: uppercase; 
    letter-spacing: .1em; 
  }
}